
import {catchError, mergeMap,  map } from 'rxjs/operators';
import { NewsService } from '../services/news.service';
import { Injectable } from '@angular/core';
import { Observable ,  of } from 'rxjs';
import { Action } from '@ngrx/store';

/*import 'rxjs/add/operator/switchMap';*/
import { switchMap } from 'rxjs/operators';



import {Actions, Effect, ofType} from '@ngrx/effects';

import * as NewsActions from './news.actions';
/*import { News } from '../../news/models/news';*/

@Injectable()
export class NewsEffects {

  constructor(
    private actions$: Actions,
    private newsService: NewsService,
  ) { }

  @Effect()
  GetMyNews$: Observable<Action> = this.actions$.
    pipe(
      ofType<NewsActions.GetMyNews>(NewsActions.GET_MY_NEWS),
      mergeMap(action =>
        this.newsService.myNews(action.payload.perPage, action.payload.currentPage).pipe(
        map((data) => {
          return new NewsActions.GetMyNewsSuccess(data);
        }),
        catchError(() => of(new NewsActions.GetMyNewsError())),)
      )
    );

  @Effect()
  GetTrashNews$: Observable<Action> = this.actions$.
    pipe(
      ofType<NewsActions.GetTrashNews>(NewsActions.GET_TRASH_NEWS),
      mergeMap(action =>
        this.newsService.trashNews(action.payload.perPage, action.payload.currentPage).pipe(
        map((data) => {
          return new NewsActions.GetTrashNewsSuccess(data);
        }),
        catchError(() => of(new NewsActions.GetTrashNewsError())),)
      )
    );

  // moves news to trash
  @Effect()
  DeleteNews: Observable<any> = this.actions$.
    pipe(
      ofType(NewsActions.DELETE_NEWS),
      map((action: NewsActions.DeleteNews) => action.payload)
    )
    .pipe(
      switchMap(payload => {
        return this.newsService.destroyNews(payload.id).pipe(
          map((response) => {
            return new NewsActions.DeleteNewsSuccess({ id: payload.id });
          }),
          catchError((error) => {
            console.log(error);
            return of(new NewsActions.DeleteNewsError());
          }),);
      })
    );

  // restore trashed news to myNews
  @Effect()
  RestoreNews: Observable<any> = this.actions$.
    pipe(
      ofType(NewsActions.RESTORE_NEWS),
      map((action: NewsActions.RestoreNews) => action.payload)
    )
    .pipe(
      switchMap(payload => {
        return this.newsService.unTrashNews(payload.id).pipe(
          map((response) => {
            return new NewsActions.RestoreNewsSuccess({ id: payload.id });
            // return new NewsActions.GetMyNews();
          }),
          catchError((error) => {
            console.log(error);
            return of(new NewsActions.RestoreNewsError());
          }),);
      })
    );

    // removes news from database
  @Effect()
  DestroyNews: Observable<any> = this.actions$
    .pipe(
      ofType(NewsActions.DESTROY_NEWS),
      map((action: NewsActions.DestroyNews) => action.payload)
    )
    .pipe(
      switchMap(payload => {
        return this.newsService.destroyNewsForever(payload.id).pipe(
          map((response) => {
            return new NewsActions.DestroyNewsSuccess({ id: payload.id });
          }),
          catchError((error) => {
            console.log(error);
            return of(new NewsActions.DestroyNewsError());
          }),);
      })
    );

  // get tickers, returned with ticker's news
  @Effect()
  GetTickers$: Observable<Action> = this.actions$.
    pipe(
      ofType<NewsActions.GetTrashNews>(NewsActions.GET_TICKERS),
      mergeMap(action =>
        this.newsService.GetTickers().pipe(
        map((data) => {
          return new NewsActions.GetTickersSuccess(data);
        }),
        catchError(() => of(new NewsActions.GetTickersError())),)
      )
    );

  // add a news to ticker
  @Effect()
  AddTickerNews$: Observable<Action> = this.actions$.
    pipe(
      ofType<NewsActions.AddTickerNews>(NewsActions.ADD_TICKER_NEWS),
      mergeMap(action =>
        this.newsService.AddTickerNews(action.payload).pipe(
        map((data) => {
          return new NewsActions.AddTickerNewsSuccess(data);
        }),
        catchError(() => of(new NewsActions.AddTickerNewsError())),)
      )
    );

  // approve a news to ticker
  @Effect()
  ApproveTickerNews$: Observable<Action> = this.actions$.
    pipe(
      ofType<NewsActions.ApproveTickerNews>(NewsActions.APPROVE_TICKER_NEWS),
      mergeMap(action =>
        this.newsService.ApproveTickerNews(action.payload.ticker_news_id).pipe(
        map((data) => {
          return new NewsActions.ApproveTickerNewsSuccess(data);
        }),
        catchError(() => of(new NewsActions.ApproveTickerNewsError())),)
      )
    );

  // dis-approve a ticker news
  @Effect()
  DisApproveTickerNews$: Observable<Action> = this.actions$.
    pipe(
      ofType<NewsActions.DisApproveTickerNews>(NewsActions.DISAPPROVE_TICKER_NEWS),
      mergeMap(action =>
        this.newsService.DisApproveTickerNews(action.payload.ticker_news_id).pipe(
        map((data) => {
          return new NewsActions.DisApproveTickerNewsSuccess(data);
        }),
        catchError(() => of(new NewsActions.DisApproveTickerNewsError())),)
      )
    );

  // delete a ticker news
  @Effect()
  DeleteTickerNews$: Observable<Action> = this.actions$.
    pipe(
      ofType<NewsActions.DeleteTickerNews>(NewsActions.DELETE_TICKER_NEWS),
      mergeMap(action =>
        this.newsService.destroyTickerNews(action.payload.ticker_news_id).pipe(
        map((data) => {
          return new NewsActions.DeleteTickerNewsSuccess(data);
        }),
        catchError(() => of(new NewsActions.DeleteTickerNewsError())),)
      )
    );

  // add a ticker
  @Effect()
  AddTicker$: Observable<Action> = this.actions$.
    pipe(
      ofType<NewsActions.AddTicker>(NewsActions.ADD_TICKER),
      mergeMap(action =>
        this.newsService.AddTicker(action.payload).pipe(
        map((data) => {
          return new NewsActions.AddTickerSuccess(data);
        }),
        catchError(() => of(new NewsActions.AddTickerError())),)
      )
    );

}
