import { Store } from '@ngrx/store';
import { Component, OnInit, Input, Output } from '@angular/core';
import { MatSnackBar, MatSnackBarConfig } from '@angular/material/snack-bar';
import { Router, ActivatedRoute } from '@angular/router';

import { News, NewsBar, TickerNews } from '../models/news';
import { NewsService } from '../services/news.service';
import { NewsState } from '../../store/app.state';
import * as newsActions from '../store/news.actions';

import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { ConfirmDialogComponent } from '../../shared/component/confirm-dialog/confirm-dialog.component';
import { Observable } from 'rxjs';

@Component({
  selector: 'app-edit',
  templateUrl: './edit.component.html',
  styleUrls: ['./edit.component.css']
})
export class EditComponent implements OnInit {

  news_bar = new NewsBar('', '');
  ticker_news = new TickerNews('');
  public new_news: News;
  edit_id = this.aRoute.snapshot.params.id;

  @Input() public dhivehiEnabled: boolean;
  @Input() public tickerEnabled = false;
  submitted = false;
  @Input() loaded = false;
  loaded2 = false;

  init_news$: any;

  constructor(
    public snackBar: MatSnackBar,
    private router: Router,
    private newsService: NewsService,
    private aRoute: ActivatedRoute,
    private store: Store<NewsState>,
    public dialog: MatDialog,


    private loaded_store: Store<any>,
  ) {
    this.loadEditNews(this.edit_id);
  }

  ngOnInit() {
    // this.changeLanguage();
    // this.toggleTicker();

    this.init_news$ = this.loaded_store.select(store => store.NewsState.isMyNewsLoaded)
      .subscribe(
        (isLoaded) => {
          if (!isLoaded) {
            console.log('News Store not Loaded, Rerouting to "My News"')
            this.router.navigate(['/news/my']);
          }
        });
  }

  loadEditNews(id: number) {
    this.loaded = false;
    this.edit_id = id;
    this.newsService.editNews(this.edit_id)
      .subscribe(
        (response) => {
          this.new_news = response;
          if ( this.new_news.language.language_code === 'Dhv' ) { this.dhivehiEnabled = true; } else { this.dhivehiEnabled = false; }
          this.loaded = true;
        },
        (error) => {
          this.submitted = false;
          const message = '';
          this.snackBar.open(message, 'dismiss');
        });
  }

  onSubmit(draft: boolean) {
    this.submitted = true;
    this.new_news.draft = draft;
    this.newsService.updateNews(this.new_news, this.edit_id)
        .subscribe(
          (response) => {
            // console.log(response);
            // this.snackBar.open('News Saved!', null, {duration: 3000});
            this.store.dispatch(new newsActions.UpdateMyNewsLoaded({ isMyNewsLoaded: false }));
            this.router.navigate(['/news/my']);
          },
          (error) => {
            this.submitted = false;
            /*let message = '';
            if (error.status === 401) {
              message = 'Sorry you do not have access to add news';
            } else {
              message = 'News Saved';
            }
            this.snackBar.open(message, 'dismiss');*/
          }
    );
  }

  cancelWrite() {
    this.router.navigate(['/news/my']);
  }

  changeLanguage() {
    if (this.dhivehiEnabled) {
      this.new_news.language.language_code = 'Dhv';
    } else {
      this.new_news.language.language_code = 'Eng';
    }
  }

  addBar() {
    const nBar = new NewsBar(null, null, null, null, null, null);
    this.new_news.newsbar.push(nBar);
  }

  removeBar(barId) {
    // this.new_news.newsbar.splice(barIndex, 1);

    let dialogRef = this.dialog.open( ConfirmDialogComponent, { width: '350px' });

    dialogRef.afterClosed().subscribe((result: string) => {
        dialogRef = null;
        if (result) { // user confirmed to delete
          this.newsService.destroyBar(barId)
          .subscribe(
            (response) => {
              /*
              ---------- // TODO
              instead of reloading the model, remove the bar from fiest loaded model ->'this.new_news'
              because now the if the user had made any changes before removing a bar,
              the changes made earlier are reloaded with the old data from database.
              */
              this.loadEditNews(this.edit_id);
              console.log(response);
            }
          );
        }
    });

  }

  addTicker() {
    const ticker = new TickerNews('');
    this.new_news.tickernews.push(ticker);
  }

  removeTicker(tickerIndex) {
    this.new_news.tickernews.splice(tickerIndex, 1);
  }

  toggleTicker() {
    if (!this.tickerEnabled) {
      this.new_news.tickernews = null;
    }else {
      const ticker = new TickerNews('');
      this.new_news.tickernews = [ticker];
    }
  }

  // TODO: Remove this when we're done, this is here for debugging purpose
  get diagnostic() { return JSON.stringify(this.new_news); }

}
