
import {take,  tap, merge, map } from 'rxjs/operators';
import { PusherService } from './../../shared/services/pusher.service';
import { Observable } from 'rxjs';
import { Component, OnInit, AfterViewInit, OnDestroy, Inject,
        ViewContainerRef, Input, Output, EventEmitter, ViewChild } from '@angular/core';
import { MatPaginator, PageEvent } from '@angular/material/paginator';
import { MatTableDataSource } from '@angular/material/table';
import { DataSource } from '@angular/cdk/table';

import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { ConfirmDialogComponent } from './../../shared/component/confirm-dialog/confirm-dialog.component';
import { ViewNewsDialogComponent } from './../../shared/component/view-news-dialog/view-news-dialog.component';

import * as newsActions from '../store/news.actions';
import { getNewsById, getMyNewsByStatus } from '../store/news.reducer';
import { Store } from '@ngrx/store';
import { News } from './../models/news';
import {Subscription} from "Rxjs";


@Component({
  selector: 'app-mynews',
  templateUrl: './mynews.component.html',
  styleUrls: ['./mynews.component.css']
})
export class MynewsComponent implements OnInit, AfterViewInit, OnDestroy {
  displayedColumns = ['news_name', 'heading', 'date', 'status', 'actions'];
  displayedColumnsPublished = ['news_name', 'heading', 'date', 'status'];
  dataSource = new NewsDataSource(this.store);
  @ViewChild(MatPaginator, {static: true}) paginator: MatPaginator;

  news$: Observable<any>;
  my_news_count = 0;
  init_news$: Subscription;
  news_page_state$: Subscription;
  pageEvent: PageEvent;

  view_dialog$: any;

  publishedNews;
  inReviewList;
  RejectedNews;

  // Pagination
  length: number;
  pageIndex = 1;
  pageSize = 100;
  pageSizeOptions = [5, 10, 25, 50, 100];

  iii: number = null;
  hoveredIndex: number = null;

  constructor(
    public dialog: MatDialog,
    private store: Store<any>,
    private pusher: PusherService
  ) { }

  newsState$: Observable<News[]>;

  ngOnInit() {
    this.init_news$ = this.store.select(store => store.NewsState.isMyNewsLoaded)
      .subscribe(
        (isLoaded) => {
          if (!isLoaded) {
            console.log('loading news');
            this.store.dispatch(new newsActions.GetMyNews({ perPage: this.pageSize, currentPage: this.pageIndex }));
            this.dialog.closeAll();
          }else {
            console.log('fetching news from store!');
            this.publishedNews = this.store.select(getMyNewsByStatus('published'));
            this.inReviewList = this.store.select(getMyNewsByStatus('ready'));
            this.RejectedNews = this.store.select(getMyNewsByStatus('reject'));

            this.dataSource = new NewsDataSource(this.store);
            this.updateNewsCount();
            this.setPagination();
          }
        }
    );

    // reload news when editor reviews the news
    this.pusher.channel.bind('App\\Events\\EditorReviewEvent', data => {
      this.store.dispatch(new newsActions.UpdateMyNewsLoaded({ isMyNewsLoaded: false }));
    });
  }

  hover(i, hoverInder) {
    this.hoveredIndex = hoverInder;
    this.iii = i;
    console.log(this.hoveredIndex);
  }

  setPagination() {
    this.news_page_state$ = this.store.select(store => store.NewsState)
    .subscribe((response) => {
      this.length = response.myNewsPageState.totalResults;
      this.pageIndex = response.myNewsPageState.currentPage;
      this.pageSize = response.myNewsPageState.perPage;
    });
  }

  onPaginateChange(event) {
    const page_number = Number(event.pageIndex) + 1;
    this.pageIndex = page_number;
    this.pageSize = event.pageSize;

    this.store.dispatch(new newsActions.UpdateMyNewsLoaded({ isMyNewsLoaded: false }));
  }

  ngAfterViewInit() {
  }

  updateNewsCount() {
    this.store.select(store => store.NewsState.myNews)
    .subscribe((response) => {
      this.my_news_count = response.length;
    });
  }

  viewNews(id: number) {
    this.view_dialog$ = this.store.select(getNewsById('myNews', id)).pipe(
      take(1))
      .subscribe((news) => {
        let dialogRef = this.dialog.open(ViewNewsDialogComponent, { width: '90%', data: news });

        dialogRef.afterClosed().subscribe((result: string) => {
          dialogRef = null;
          if (result) {
          }
        });
        dialogRef.componentInstance.onDelete.subscribe((result: any) => {
          this.deleteNews(result);
        });
      });
    return false;
  }

  deleteNews(id: number) {
  console.log('doing delete');
    let dialogRef = this.dialog.open(ConfirmDialogComponent,
    { width: '350px', data: {title: 'Delete News', message: 'Are you sure you want to delete this news?'} });

    dialogRef.afterClosed().subscribe((result: string) => {
      dialogRef = null;
      if (result) {
        this.store.dispatch(new newsActions.DeleteNews({id: id}));
      }
    });
  }

  ngOnDestroy() {
    this.init_news$.unsubscribe();
    this.news_page_state$.unsubscribe();
  }

}

export class NewsDataSource extends DataSource<any> {
  constructor(private store: Store<any>) {
    super();
  }
  connect(): Observable<News[]> {
    return this.store.select(store => store.NewsState.myNews);
  }
  disconnect() {}
}
