import { Component, OnInit } from '@angular/core';
import { NewsService } from '../services/news.service';
import { JournalistDashboard, News } from '../models/news';

import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { ConfirmDialogComponent } from './../../shared/component/confirm-dialog/confirm-dialog.component';
import { ViewNewsDialogComponent } from './../../shared/component/view-news-dialog/view-news-dialog.component';

@Component({
  selector: 'app-news-dashboard',
  templateUrl: './dashboard.component.html',
  styleUrls: ['./dashboard.component.css']
})
export class DashboardComponent implements OnInit {
  dashboardData: JournalistDashboard = new JournalistDashboard();
  isDataLoaded = false;

  constructor(
    private newsService: NewsService,
    public dialog: MatDialog,
  ) { }

  ngOnInit() {
    this.newsService.getDashboard().subscribe((data: JournalistDashboard) =>{
      this.dashboardData = data;
      this.isDataLoaded = true;
    });
  }


  viewNews(news: News) {
    let dialogRef = this.dialog.open(ViewNewsDialogComponent, { width: '90%', data: news });
  }

}
