import { Message } from './../../apps/mail/message';
import { Component, OnInit, Input } from '@angular/core';
import { FormBuilder, FormGroup } from '@angular/forms';
import { FormControl, Validators } from '@angular/forms';
import { MatSnackBar, MatSnackBarConfig } from '@angular/material/snack-bar';
import { Router } from '@angular/router';

import { News, NewsBar, TickerNews } from '../../news/models/news';
import { NewsService } from '../../news/services/news.service';

import * as newsActions from '../store/news.actions';
import { Store } from '@ngrx/store';

@Component({
  selector: 'app-news-write',
  templateUrl: './write.component.html',
  styleUrls: ['./write.component.css']
})
export class WriteComponent implements OnInit {

  news_bar = new NewsBar('', '-');
  ticker_news = new TickerNews('');
  // new_news = new News(null, null, null, null, null, { language_code: 'Dhv' }, [this.news_bar], [this.ticker_news]);
  new_news = new News();

  @Input() public dhivehiEnabled = true;
  @Input() public tickerEnabled = false;
  submitted = false;

  constructor(
    public snackBar: MatSnackBar,
    private router: Router,
    private newsService: NewsService,
    private store: Store<any>
  ) {}

  ngOnInit() {
    this.changeLanguage();
    this.toggleTicker();
  }

  onSubmit(draft?: boolean) {
    this.submitted = true;
    this.new_news.draft = draft;

    this.new_news.newsbar.forEach(bar => {
      bar.detail = bar.title;
    });

    this.newsService.saveNews(this.new_news)
        .subscribe(
          (response) => {
            this.store.dispatch(new newsActions.UpdateMyNewsLoaded({ isMyNewsLoaded: false }));
            this.router.navigate(['/news/my']);
          },
          (error) => {
            this.submitted = false;
          }
    );
  }

  cancelWrite() {
    this.router.navigate(['/news/my']);
  }

  changeLanguage() {
    if (this.dhivehiEnabled) {
      this.new_news.language.language_code = 'Dhv';
    } else {
      this.new_news.language.language_code = 'Eng';
    }
  }

  addBar() {
    const newBar = new NewsBar('', '-');
    this.new_news.newsbar.push(newBar);
  }

  removeBar(barIndex) {
    this.new_news.newsbar.splice(barIndex, 1);
  }

  addTicker() {
    const ticker = new TickerNews('');
    this.new_news.tickernews.push(ticker);
  }

  removeTicker(tickerIndex) {
    this.new_news.tickernews.splice(tickerIndex, 1);
  }

  toggleTicker() {
    if (!this.tickerEnabled) {
      this.new_news.tickernews = null;
    }else {
      const ticker = new TickerNews('');
      this.new_news.tickernews = [ticker];
    }
  }

  // TODO: Remove this when we're done
  get diagnostic() { return JSON.stringify(this.new_news); }

}
