
import {take} from 'rxjs/operators';
import { Observable } from 'Rxjs';
import { Component, OnInit, OnDestroy, Inject, ViewContainerRef } from '@angular/core';
import { PageEvent } from '@angular/material/paginator';
import { MatTableDataSource } from '@angular/material/table';
import { NewsService } from '../services/news.service';
import { DataSource } from '@angular/cdk/table';

import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { ConfirmDialogComponent } from '../../shared/component/confirm-dialog/confirm-dialog.component';
import { ViewNewsDialogComponent } from '../../shared/component/view-news-dialog/view-news-dialog.component';

import { getNewsById } from '../store/news.reducer';
import * as newsActions from '../store/news.actions';
import { Store } from '@ngrx/store';
import { News } from '../models/news';



@Component({
  selector: 'app-trash',
  templateUrl: './trash.component.html',
  styleUrls: ['./trash.component.css']
})
export class TrashComponent implements OnInit, OnDestroy {
  displayedColumns = ['news_name', 'heading', 'date', 'status', 'actions'];
  dataSource = new NewsDataSource(this.store);
  trash_news_count = 0;

  init_news$: any;
  news_page_state$: any;
  view_news_dialog$: any;
  pageEvent: PageEvent;

  // Pagination
  length: number;
  pageIndex = 1;
  pageSize = 10;
  pageSizeOptions = [5, 10, 25, 50, 100];

  constructor(
    private newsService: NewsService,
    public dialog: MatDialog,
    private store: Store<any>,
    private viewContainerRef: ViewContainerRef
  ) { }

  ngOnInit() {
    this.init_news$ = this.store.select(store => store.NewsState.isTrashLoaded)
      .subscribe(
        (isLoaded) => {
          if (!isLoaded) {
            this.store.dispatch(new newsActions.GetTrashNews({ perPage: this.pageSize, currentPage: this.pageIndex }));
          }else {
            this.dataSource = new NewsDataSource(this.store);
            this.updateNewsCount();
            this.setPagination();
            this.dialog.closeAll();
          }
        }
    );
  }

  ngOnDestroy() {
    this.init_news$.unsubscribe();
    //this.news_page_state$.unsubscribe();
  }

  setPagination() {
    this.news_page_state$ = this.store.select(store => store.NewsState)
      .subscribe((response) => {
        this.length = response.trashNewsPageState.totalResults;
        this.pageIndex = response.trashNewsPageState.currentPage;
        this.pageSize = response.trashNewsPageState.perPage;
      });
  }

  onPaginateChange(event) {
    const page_number = Number(event.pageIndex) + 1;
    this.pageIndex = page_number;
    this.pageSize = event.pageSize;
    this.store.dispatch(new newsActions.UpdateTrashNewsLoaded({ isTrashNewsLoaded: false }));
  }

  updateNewsCount() {
    this.store.select(store => store.NewsState.trashNews)
    .subscribe((response) => {
      this.trash_news_count = response.length;
    });
  }

  viewNews(id: number) {
    // this.newsService.getNews(id)
    //   .subscribe(
    //     (response) => {
    //       const dialog_news = response;
    //       let dialogRef = this.dialog.open(ViewNewsDialogComponent,
    //         { width: '90%', data: dialog_news });

    //       dialogRef.afterClosed().subscribe((result: string) => {
    //         dialogRef = null;
    //         if (result) {
    //           // actions from dialog if any
    //           console.log(result);
    //         }
    //       });
    //       // user called for delete
    //       dialogRef.componentInstance.onDelete.subscribe((result: number) => {
    //         this.deleteNews(result);
    //       });
    //       // user called for undo delete
    //       dialogRef.componentInstance.onUndo.subscribe((result: number) => {
    //         this.undo(result);
    //       });
    //     }
    // );

    this.view_news_dialog$ = this.store.select(getNewsById('trashNews', id)).pipe(
      take(1))
      .subscribe((news) => {
            let dialogRef = this.dialog.open(ViewNewsDialogComponent, { width: '90%', data: news });

            dialogRef.afterClosed().subscribe((result: string) => {
              dialogRef = null;
              this.view_news_dialog$.unsubscribe();
              console.log('unsubscribed');
              if (result) {
                console.log(result);
              }
            });
            // user called for delete (destroy)
            dialogRef.componentInstance.onDelete.subscribe((result: number) => {
              this.deleteNews(result);
            });
            // user called for undo delete
            dialogRef.componentInstance.onUndo.subscribe((result: number) => {
              this.undo(result);
            });
      });
    return false;
  }

  deleteNews(id: number) {

    let dialogRef_del = this.dialog.open( ConfirmDialogComponent,
      { width: '350px', data: {title: 'Delete News', message: 'Are you sure you want to delete this news forever?'} });

      dialogRef_del.afterClosed().subscribe((result: string) => {
        dialogRef_del = null;
        if (result) { // user confirmed to delete
          this.store.dispatch(new newsActions.DestroyNews({ id: id }));
        }
    });
  }

  undo(id: number): void {
    this.store.dispatch(new newsActions.RestoreNews({ id: id }));
  }

}

export class NewsDataSource extends DataSource<any> {
  constructor(private store: Store<any>) {
    super();
  }
  connect(): Observable<News[]> {
    return this.store.select(store => store.NewsState.trashNews);
  }
  disconnect() {}
}
